import { Component, OnInit } from '@angular/core';
import { DatabaseService } from '../services/dbService/database.service';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';

@Component({
  templateUrl: './display-table.component.html',
  styleUrls: ['./display-table.component.css']
})
export class DisplayTableComponent implements OnInit {
  orders = [];
  response: any;

  constructor(private _dbService: DatabaseService) {
   }

  ngOnInit() {
    this._dbService.getOrdersData()
      .subscribe(res => this.orders = res.message.data,
                 error => console.log(error));
  }

  updateOrder(order: any): void {
    console.log('order:', order);
    this._dbService.updateOrder(order)
      .subscribe(res =>  this.orders = res.message.data,
                         error => console.log(error));

    this._dbService.getOrdersData()
      .subscribe(res => this.orders = res.message.data,
                 error => console.log(error));
  }

  generateReport(): void {
    new Angular2Csv(this.orders, 'My Report');
  }
}
