import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';

@Injectable()
export class DatabaseService {

  private _url = 'https://faasos-server.herokuapp.com/';
  dishes: Array<string> = ['a', 'b', 'c'];

  constructor(private _http: HttpClient) { }

  getDishNames(): Observable<any> {
    return this._http.get<any>(this._url + '/getDishes');
  }

  getOrdersData(): Observable<any> {
    return this._http.get<any>(this._url + '/getOrders');
  }

  placeOrder(orderForm: any): Observable<any> {
    console.log('order in service:', orderForm);
    return this._http.post<any>(this._url + '/placeOrder', orderForm);
  }

  predictOrders(predictionForm: any): Observable<any> {
    console.log('prediction in service:', predictionForm);
    return this._http.post<any>(this._url + '/predict', predictionForm);
  }

  updateOrder(order: any): Observable<any> {
    return this._http.post<any>(this._url + '/updateOrder', order);
  }
}
