import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { OrderComponent } from './order/order.component';
import { SetPredictionComponent } from './set-prediction/set-prediction.component';
import { NavbarComponent } from './navbar.component';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { DatabaseService } from './services/dbService/database.service';
import { OrderModel } from './order/order-model';
import { DisplayTableComponent } from './display-table/display-table.component';
import { HomeComponent } from './home.component';
// import { WebSocketService } from './services/wsService/web-socket.service';



@NgModule({
  declarations: [
    AppComponent,
    OrderComponent,
    SetPredictionComponent,
    NavbarComponent,
    DisplayTableComponent,
    HomeComponent
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    HttpClientModule,
    RouterModule.forRoot([
      { path: 'order', component: OrderComponent },
      { path: 'predict', component: SetPredictionComponent },
      { path: 'display', component: DisplayTableComponent },
      { path: 'home', component: HomeComponent },
      { path: '', component: HomeComponent },
    ])
  ],
  providers: [DatabaseService],
  bootstrap: [AppComponent]
})
export class AppModule { }
