import { Component, OnInit } from '@angular/core';
import { DatabaseService } from 'src/app/services/dbService/database.service';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  templateUrl: './set-prediction.component.html',
  styleUrls: ['./set-prediction.component.css']
})
export class SetPredictionComponent implements OnInit {

  dishes: string[] = [];
  response: any;
  orderPrediction = new FormGroup({
    dish : new FormControl(),
    quantity : new FormControl(),
  });
  constructor(private _dbService: DatabaseService) { }

  ngOnInit() {
    this._dbService.getDishNames()
    .subscribe(data => this.dishes = data.message.data,
               error => console.log(error));
  }

  selectChange(event): void {
    this.orderPrediction.patchValue({
      dish: event.target.value
    });
  }

  makePrediction(predictionForm: any): void {
    this._dbService.predictOrders(predictionForm)
      .subscribe(response => this.response = response,
                 error => console.log('error:', error)
      );
  }
}
