import { Component, OnInit } from '@angular/core';
import { OrderModel } from './order-model';
import { FormControl, FormGroup } from '@angular/forms';
import { DatabaseService } from 'src/app/services/dbService/database.service';

@Component({
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {

  dishes: string[] = [];
  response: any;

  order = new FormGroup({
    dish : new FormControl(),
    quantity : new FormControl(),
  });

  constructor(private _dbService: DatabaseService) { }

  ngOnInit() {
    this._dbService.getDishNames()
      .subscribe(response => this.dishes = response.message.data,
                 error => console.log(error));
  }

  selectChange(event): void {
    this.order.patchValue({
      dish: event.target.value
    });
  }

  submitOrder(orderForm: any): void {
    this._dbService.placeOrder(orderForm)
      .subscribe(response => this.response = response,
                 error => console.log(error));
  }
}
